import './App.css';
import IndexListComponent from './components/IndexListComponent'
import DisplayPostComponent from './components/DisplayPostComponent'

import { Route, Switch } from 'react-router-dom';
import HomepageLayout from './components/HomepageLayout';
import PremiumPage from './components/PremiumPage';
import DonatePage from './components/DonatePage';


function App() {
  

  return (
    <div className="App">
      <Switch>
        {/* <Route path="/" render={(props) => <IndexListComponent id="system_design" />} exact /> */}
        <Route path="/snrsde/system_design" render={(props) => <IndexListComponent id="system_design" title ='System Design' />} exact />
        <Route path="/snrsde/ood" render={(props) => <IndexListComponent id="ood" title ='Object Oriented Design' />} exact />
        <Route path="/snrsde/algo" render={(props) => <IndexListComponent id="algo" title ='Algo/DS' />} exact />
        <Route path="/snrsde/ambiguity" render={(props) => <IndexListComponent id="ambiguity" title ='Ambiguity' />} exact />
        <Route path="/snrsde/" component={HomepageLayout} exact />
        <Route path="/snrsde/premium" component={PremiumPage} exact />
        <Route path="/snrsde/donate" component={DonatePage} exact />
        <Route path="/snrsde/post/:id" component={DisplayPostComponent} exact/>
      </Switch>
    </div>
  );
}

export default App;

